import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import noneImage from '../../../assets/none-png-2.png'
import { tvApi, moviesApi } from '../../../api'

const Root = styled.div`
    border-radius:0 0 5px 5px;
    width:100%;
    background:rgba(255,255,255,0.2);
    animation:0.5s all;
    overflow: auto;
        -ms-overflow-style: none;
        &::-webkit-scrollbar {
            background:rgba(0,0,0,0.1)
        }
        &::-webkit-scrollbar-thumb {
            background: rgba(255,255,255,0.2);
    }
        &::-webkit-scrollbar-thumb:hover {
            background: rgba(255,255,255,0.5);
    }
    @media screen and (max-width:768px) {
        background:rgba(255,255,255,0.2)
    }
    animation:loadRoot1 1s ease-in-out ;
    max-height:200px;
    @keyframes loadRoot1 {
        0%{
            max-height:0px;
        }
    }
`

const Container = styled.div`
    text-align:center;
    margin:10px;
    font-size:20px;
    display:flex;
    align-items:center;
    @media screen and (max-width:768px) {
        font-size:16px;
    }
    
    
`
const Image = styled.img`
    display:block;
    width:30px;
    height:30px;
    margin-right:15px;
`

const Company = (props) => {
    const { match: { params: { id, type } } } = props
    const parsedId = parseInt(id)
    const [state, setState] = useState({ loading: true, error: null, result: null })

    useEffect(() => {
        const getPerson = async () => {
            try {
                if (!isNaN(parsedId) && type === 'movie') {
                    const { data: { production_companies: result } } = await moviesApi.movieDetail(parsedId)
                    setState(prev => { return { ...prev, result, loading: false } })
                }
                if (!isNaN(parsedId) && type === 'show') {
                    const { data: { production_companies: result } } = await tvApi.tvDetail(parsedId)
                    setState(prev => { return { ...prev, result, loading: false } })
                }
            }
            catch (error) {
                setState(prev => { return { ...prev, error, loading: false } })
            }
        }
        getPerson()
    }, [parsedId, type])
    const { loading, error, result } = state
    return (
        loading ? null : error ? null : result &&
            <Root>
                {result && result.map((i, index) =>
                    <Container key={index}>
                        <Image src={i.logo_path ? `https://image.tmdb.org/t/p/original/${i.logo_path}` : noneImage} />
                        {i.name}
                    </Container>)}
            </Root>
    )
}

export default Company
