import React, { useState, useEffect } from 'react'
import { tvApi } from '../../../api'
import SeasonPresenter from './SeasonPresenter'

const SeasonContainer = (props) => {
    const { match: { params: { id, season, title } } } = props
    const [state, setState] = useState({ loading: true, error: null, data: null })
    useEffect(() => {
        const parsedId = parseInt(id)
        const parsedSeason = parseInt(season)
        const getPerson = async () => {
            try {
                const { data } = await tvApi.tvSeasons(parsedId, parsedSeason)
                setState(prev => { return { ...prev, data, loading: false, title } })
            } catch (error) {
                setState(prev => { return { ...prev, error, loading: false } })
            }
        }
        getPerson()
    }, [id, season, title])
    const { loading, error, data } = state
    return (
        <SeasonPresenter loading={loading} error={error} result={data} title={title} />
    )
}

export default SeasonContainer

