import React from 'react'
import styled from 'styled-components'
import noneImage from '../assets/none-png-2.png'
import { Link } from 'react-router-dom'

const Container = styled.div`
    font-size:12px;
    a{
        color:inherit;
        text-decoration:none;
        font-size:inherit;
        font:inherit
    }
`

const Image = styled.div`
    height:250px;
    width:160px;
    margin:0 5px;
    background-size: cover;
    background-position: center center;
    border-radius: 4px;
    transition: 0.1s linear;
    background-image:url(${props => props.bgUrl});
    @media screen and (max-width:768px){
        height:100px;
        width:70px;
        margin:0 2px;
        border-radius:2px;
    }
`

const ImageContainer = styled.div`
        margin-bottom: 5px;
        position: relative;
`

const Title = styled.span`
    font-weight:700;
    text-align:center;
    display: block;
    margin-bottom: 3px;
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap;
    @media screen and (max-width:768px) {
        margin:auto;
        width:69px;
    }
    `
const Recommendations = ({ id, imgUrl, title, isMovie }) => {
    return (
        id && imgUrl && title &&
        <Link to={isMovie === true ? `/movie/${id}/` : isMovie === false ? `/show/${id}/` : '/error/'}>
            <Container>
                <ImageContainer>
                    <Image bgUrl={imgUrl && imgUrl ? `https://image.tmdb.org/t/p/w500${imgUrl}` : noneImage} />
                </ImageContainer>
                <Title>{title && title.length > 10 ? title.substring(0, 10) + '...' : title}</Title>
            </Container>
        </Link>)
}

export default Recommendations