import React from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'
import Section from '../../Components/Section'
import Loader from '../../Components/Loader'
import Poster from '../../Components/Poster'
import { Helmet } from 'react-helmet'
import Error from '../../Components/Error'
import PopularComponent from '../../Components/PopularComponent'

const Container = styled.div`
    padding:50px 60px 70px;
    @media screen and (max-width:768px) {
        padding:5px
    }
`

const HomePresenter = ({ nowPlaying, upcoming, popular, error, loading, randomId }) => {
    return (
        loading ? <Loader /> : error ? <Error data={'No Data'} /> : (
            <Container>
                <Helmet>
                    <title>
                        영화 | Maflix
                    </title>
                </Helmet>
                <PopularComponent id={popular[randomId].id} />
                {nowPlaying && nowPlaying.length > 0 && (
                    <Section section={'/movie/sub/now/'} title={'현재 상영중'}>{nowPlaying.map(i => <Poster key={i.id} id={i.id} imgUrl={i.poster_path} title={i.title} rating={i.vote_average} year={i.release_date && i.release_date.substring(0, 4)} isMovie={true} />)}</Section>
                )}
                {upcoming && upcoming.length > 0 && (
                    <Section section={'/movie/sub/up/'} title={'개봉 예정'}>{upcoming.map(i => <Poster key={i.id} id={i.id} imgUrl={i.poster_path} title={i.title} rating={i.vote_average} year={i.release_date && i.release_date.substring(4)} isMovie={true} />)}</Section>
                )}
                {popular && popular.length > 0 && (
                    <Section section={'/movie/sub/popmovie/'} title={'훙행순'}>{popular.map(i => <Poster key={i.id} id={i.id} imgUrl={i.poster_path} title={i.title} rating={i.vote_average} year={i.release_date && i.release_date.substring(4)} isMovie={true} />)}</Section>
                )}
                {error && <Error color='#e74c3c' data={'No data'} />}
            </Container>
        ))
}


HomePresenter.propTypes = {
    nowPlaying: PropTypes.array,
    upcoming: PropTypes.array,
    popular: PropTypes.array,
    error: PropTypes.string,
    loading: PropTypes.bool.isRequired
}

export default HomePresenter